
@font-face {
    font-family: 'AcquestScript';
        src: local('AcquestScript'),
            url('../img/AcquestScript.ttf'); 
}

/* Определяем номер сертификата */
.userId {
    font-size: 18px;
    position: absolute;
    top: 34%;
    left: 60%;
    color: #191970;
}

/* Определяем имя получателя */
.userName {
    font-size: 50px;
    font-family: AcquestScript;
    position: absolute;
    transform: translate(-50%, -50%);
    top: 66%;
    left: 50%;
    color: #191970;
}
/* Определяем сегодняшнюю дату */
.currentDate {
    font-size: 20px;
    position: absolute;
    top: 82%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #191970;
}